.TabsCaruselControl {
  display: flex;
  align-items: center;
}

.TabsCaruselControl h4{
  margin: 20px 10px;
}

.Tabs__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.Tabs__items {
  margin: 0;
  margin-right: -1px;
  padding: 0;
  display: inline-block;
}

.button {
  padding: 10px 30px;
  color: #3b3be9;
  background: #fff;

  border: 1px solid #dfdfdf;
  cursor: pointer;

  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
}

.button:hover {
  color: #000;
  background-color: #dfdfdf;
}

.button--active {
  color: #000;
  background-color: #ffffff;
  border-bottom: 1px solid #ffffff;
}

.Tabs-content {
  max-width: 600px;
  border-top: 1px solid #dfdfdf;
  margin: 0;
  margin-top: -1px;
  padding: 15px;
}

.Tabs-content p{
  margin: 0;
  padding: 0;
}

.hiden {
  display: none;
}

